import Col from 'react-bootstrap/Col';
import Carousel from 'react-bootstrap/Carousel';
import Accordion from 'react-bootstrap/Accordion';

import {
  ProjectsContainer3,
  ProjectsHeader3,
  CarouselWrapper3,
  ProjectTitle3,
  ProjectContainer3,
  ProjectDescription3,
} from './cstyles';
import data from '../../data';

const Comm = ({ length, comm, index }) => (
  <ProjectContainer3>
    <Col md={10} xs={12}>
      <ProjectTitle3>{comm.title}</ProjectTitle3>
      <CarouselWrapper3>
        <Carousel interval={5000}>
          {comm.pictures.map((pic, index) => (
            <Carousel.Item>
              <img className='w-100' src={pic} alt={`${index} slide`} width="500" height={"600"}/>
            </Carousel.Item>
          ))}
        </Carousel>
      </CarouselWrapper3>
      <ProjectDescription3>{comm.description}</ProjectDescription3>
      {index < length - 1 ? <hr className='project-divider' /> : null}
    </Col>
  </ProjectContainer3>
);

const Comms = () => {
  const { comms } = data;
  const { body } = comms;
  return (
    <ProjectsContainer3>
      <Col md={8} xs={12}>
        <Accordion.Item eventKey='5'>
          <Accordion.Header>
            <ProjectsHeader3>{comms.header}</ProjectsHeader3>
          </Accordion.Header>
          <Accordion.Body>
            {body.map((comm, index) => (
              <Comm length={body.length} comm={comm} index={index} />
            ))}
          </Accordion.Body>
        </Accordion.Item>
      </Col>
    </ProjectsContainer3>
  );
};

export default Comms;
